// db.js
import Dexie from 'dexie'

const db = new Dexie('jdzjdb')

db.version(1).stores({
  vodhistory: 'vodId, vodName, percent, cover, selected, datetime, esptitle, playIndex, playPos, sourceIndex, watchTime, year, director',
  searchword: 'word,wid',
  vodlike: 'vodId, vodName, cover, selected, datetime, year, director',
  voddown: 'originUrl,playUrl,vodId,name,source,percent,diskPath,cover,downLength,ext,status,datetime,selected,esp,yat,director',
})

// 打开数据库
db.open().catch((e) => {
  console.error('Open failed: ' + e)
})

export default db

